import { connectStateResults } from 'react-instantsearch-dom';

const BlogSearchResults = ({ searchState, searchResults, children, query }) => {
  console.log('searchState', searchState);
  console.log('searchResults', searchResults);
  if(searchState.query && searchResults && searchResults.query === searchState.query && searchResults.nbHits !== 0) {
    return children;
  }
  return null;
};

export default connectStateResults(BlogSearchResults);
